.bodyShadow {
  background-color: rgba(51, 51, 51, 0.753);
}

.zoom {
  animation: zoomIN 0.2s;
}
body {
  max-width: 100vw;
  overflow-x: hidden;
  margin: 0;
}
* {
  max-width: 100vw;
}
@keyframes zoomIN {
  0% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(1);
  }
}

.cursor-direction {
  position: fixed;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  pointer-events: none;
  mix-blend-mode: multiply;
  z-index: 99999;
  transition: transform 0.5s;
}

.swiper-button-next,
.swiper-button-prev {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
}
.swiper-button-next::after {
  content: "🡢" !important;
  transform: translate(-7px, -9px) !important;
  /* transform: rotate(180deg) !important; */
}
.swiper-button-prev::after {
  content: "🡠" !important;
  width: 30px !important;
  transform: translate(-7px, -10px) !important;
}
